.App {
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgb(87, 111, 156)),
    to(rgb(42, 60, 92))
  );
  background: -webkit-linear-gradient(
    top,
    rgb(87, 111, 156) 0%,
    rgb(42, 60, 92) 100%
  );
  background: -o-linear-gradient(
    top,
    rgb(87, 111, 156) 0%,
    rgb(42, 60, 92) 100%
  );
  background: linear-gradient(
    to bottom,
    rgb(87, 111, 156) 0%,
    rgb(42, 60, 92) 100%
  );

  font-size: 24px;
  height: 100vh;
  min-height: 550px;
}

.App-image {
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}

.App-grid {
  height: 100%;
  opacity: 0;
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  grid-template-columns: 1fr;
  place-items: center;
  grid-template-areas:
    "."
    "info"
    "info "
    "feed"
    "feed"
    ".";
}

.App-info {
  grid-area: info;
}

.App-links {
  margin-top: 20px;
}

.App-feed {
  grid-area: feed;
  width: 100%;
}

.App-enter {
  opacity: 0.01;
}

.App-enter-done {
  opacity: 1;
  -webkit-transition: all 500ms ease-in;
  -o-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
}



